$backgroundColor: #ffffff;
$bodyColor: #000000;
$bodyFont: -apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,Arial,sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol";

$mainBackground: #e0e0e0;
$secondaryBackground: #0e0e0e;

html {
  box-sizing: border-box;
  font-size: 16px;
  height: 100%;
}

body {
  height: 100%;
  min-height: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol, ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  background: $backgroundColor;
  color: $bodyColor;
  font-family: $bodyFont;
  margin: 0;
  padding: 0;
}

.main {
  display: flex;
  background: $mainBackground;
  min-height: 100%;
  position: relative;
}

.main-header {
  border: 1px solid red;
  width: 35%;
  background: $secondaryBackground;
  height: 20em;
  color: white;
  text-align: center;
      justify-content: flex-end;
    flex-direction: column;
    display: flex;
}

.main-content {
  border: 1px solid blue;
  width: 65%;
  max-width: 1024px;
  padding: 1rem;
}

.main-footer {
  background-color: black;
  border: 1px solid green;
  position: absolute;
  top: 20em;
  bottom: 0;
  width: 35%;
  background: $secondaryBackground;
  text-align: center;
}

p {
  margin-bottom: 1em;
}

// HEADER
#avatar {
  height: 180px;
  border-radius: 100%;
  border: 5px solid white;
  margin-bottom: 40px;
  height: 10em;
border-radius: 100%;
border: 5px solid white;
margin-bottom: 40px;
width: 10em;
align-self: center;
}

// FOOTER
ul {
  list-style: none;
  padding: 0;
}
li {
  border-radius: 100%;
  border: 2px solid white;
  color: white;
  margin: 8px;
  display: inline-block;
  list-style: none;
  width: 65px;
  height: 65px;
}
li:hover {
  color: white;
  text-decoration: none;
  -webkit-transition: 500ms;
  -moz-transition: 500ms;
  transition: 500ms;
  text-decoration: none;
  background-color: rgba(0, 0, 0, 0.5);
}
a {
  color: white;
  padding: 15px;
  text-decoration: none;
}
.description {
  display: none;
}
